import React from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import useMediaQuery from '@material-ui/core/useMediaQuery'
import { useTheme } from '@material-ui/core/styles'
let message1 = 'Duplicate CPT code and duplicate room type'
let message2 = 'Duplicate CPT code'

export default function AlertDialog ({open = false, setOpen, type}) {
  const theme = useTheme()

  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'))

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby='responsive-dialog-title'
      >
        <DialogTitle id='responsive-dialog-title' color='danger'> { type === 'hospitalStandardRates' ? message1 : message2 }  </DialogTitle>
        {/* <DialogContent>
          <DialogContentText>
          Duplicate CPT code and duplicate room type
          </DialogContentText>
        </DialogContent> */}
        <DialogActions>
          <Button onClick={handleClose} color='primary' autoFocus>
            ok
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
