import React, { useState } from 'react'

export default function UseInputLimitHook (onChange, max = null, textFieldRef) {
  const [num, setNum] = useState('')
  const onChangeInputLimit = e => {
    const { target: { value } } = e

    if (max && value > max) {
        textFieldRef.current.value = num
    } else if(value < 0) {
      textFieldRef.current.value = ''
    } else {
      if(value) {
        setNum(value)
        textFieldRef.current.value = +value
        onChange(e)
      } else {
        textFieldRef.current.value = null
      }
    }
  }
  return [onChangeInputLimit]
}
